@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap');

/*Variables */
html {
  --mobile-width: 375px;
  --desktop-width: 1440px;
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 16px;
};

.cardContent > p {
  color: white;
}

.App {
  display: flex;
  min-height: 100vh;
  text-align: center;
  justify-content: center;
  align-items: center;
  background: var(--pale-blue);
  background-image: url('./images/pattern-background-desktop.svg');
  background-repeat: no-repeat;
  max-width: var(--desktop-width);
}

.card {
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: none;
  border-radius: 1.5rem;
  /* box-shadow: 0px 1px 15px 4px var(--desaturated-blue); */
box-shadow: 2px 27px 27px -10px rgba(171,171,171,0.47);  max-width: 400px;
}

.cardContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0rem 30px;
  color: var(--desaturated-blue);
}

.cardContent > h2 {
  color: var(--dark-blue);
  margin-bottom: 0px;
}

.card > h1 h2 {
  color: var(--dark-blue);
  font-weight: 900;
}

.card > a {
  font-weight: 900;
  color: var(--desaturated-blue)
}

.heroImage {
  width: 100%;
  max-height: auto;
  object-fit: contain;
  border-radius: 1rem 1rem 0 0 ;
}

.planSelection {
  display: flex;
  flex-direction: row;
  border: none;
  border-radius: 0.6rem;
  background-color: var(--very-pale-blue);
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 1rem 0rem;
  margin-bottom: 1rem;
}

.planSelection a {
  font-weight: 500;
  color: var(--bright-blue);
}

.planText {
  margin-left: 20px;
}

.planText h4{
  color: var(--dark-blue);
}

.changeLink {
  margin-left: 6rem;
  font-size: 14px;
  font-weight: 700;
}

.changeLink:hover {
  color: hsl(245, 75%, 68%);
  text-decoration: none;
  font-weight: 900;
}

.planType {
  margin: 0;
}

.price{
  margin: 0;
}

.proceed {
  color: white;
  font-family: 'Red Hat Display';
  font-weight: 700;
  background-color: var(--bright-blue);
  width: 100%;
  padding: 1rem;
  margin-top: 10px;
  border: none;
  border-radius: 1rem;
box-shadow: 2px 20px 27px -10px var(--desaturated-blue);}

.proceed:hover {
  background-color: hsl(245, 75%, 68%);
}

.cancelOrder {
  text-decoration: none;
  width: fit-content;
  padding: 1rem;
  margin: 10px 0px;
  font-size: 14px;
  font-weight: 900;
  color: var(--desaturated-blue)
}

.cancelOrder:hover {
  color: var(--dark-blue);
}
